body{box-sizing:border-box;background-image:url("./images/background.png");background-repeat:no-repeat;background-position:left top;background-size:25%;background-color:rgba(245,197,228,.2)}.container{display:flex;flex-direction:column;align-items:center;text-align:center}.container p{font-family:"Fasthand",cursive}.quote-box{max-width:800px;width:50%;margin-top:50px;padding:30px;font-family:Georgia,"Times New Roman",Times,serif;box-shadow:#f4cae4 0px 20px 30px -10px;border-radius:10px}.quote-box blockquote{border:none;margin-bottom:-30px;quotes:"“" "”" "‘" "’";line-height:1.5}.quote-box blockquote h2{font-size:18px}.quote-box blockquote h2:before{content:open-quote;font-size:30px}.quote-box figcaption{text-align:right;line-height:4}.social{display:flex}.social img{image-rendering:high-quality;padding:10px;opacity:1}.social button{font-family:Georgia,"Times New Roman",Times,serif;font-weight:bold;margin-left:auto;border:none;border:1px solid #f4cee1;background-color:rgba(0,0,0,0);height:50px;border-radius:10px;box-shadow:rgba(238,227,193,.2) 0px 8px 24px;animation:buttonFloat 2s infinite forwards}.social button:hover{cursor:pointer}@keyframes buttonFloat{25%{transform:rotate(-5deg)}75%{transform:rotate(5deg)}}.flower-container{position:fixed;top:0;left:0;width:100%;height:100%;pointer-events:none}.flower{position:absolute;top:0;left:0;width:2%;height:2%;background-image:url("./images/peach.png");background-size:cover;z-index:9999;animation-name:fall;animation-timing-function:linear;pointer-events:none;border-radius:5px;border-top-left-radius:50%;border-bottom-right-radius:50%;opacity:.5}@keyframes fall{0%{top:-50px}100%{top:calc(100vh + 50px)}}